import axios from "axios";

function request (params) {
    return new Promise((resolve, reject) => {
        axios.request(params).then((res) => {
            resolve(res);
        }).catch((err) => {
            const errData = (err && err.response && err.response.data) || {
                msg: "request fail",
                data: {}
            };
            reject(errData);
        });
    });
}

export default request;



