<template>
    <div class="menu-wrap">
        <md-speed-dial md-event="click">
            <md-speed-dial-target>
                <md-icon class="md-morph-initial">
                    <i class="iconfont icon-menu"></i>
                </md-icon>
                <md-icon class="md-morph-final">
                    <i class="iconfont icon-menu"></i>
                </md-icon>
            </md-speed-dial-target>

            <md-speed-dial-content>
                <md-button class="md-icon-button">
                    <i class="iconfont icon-home" @click="switchPage()"></i>
                </md-button>
                <md-button class="md-icon-button">
                    <i class="iconfont icon-create" @click="switchPage('create')"></i>
                </md-button>
                <md-button class="md-icon-button">
                    <i class="iconfont icon-list" @click="switchPage('list')"></i>
                </md-button>
                <md-button class="md-icon-button">
                    <i class="iconfont icon-document" @click="openDocs"></i>
                </md-button>
            </md-speed-dial-content>
        </md-speed-dial>
    </div>
</template>

<script>
export default {
    data() {
        return {};
    },
    methods: {
        switchPage(targetPage = "") {
            this.$router.push({ path: `/${targetPage}` });
        },
        openDocs() {
            window.open(
                "http://galaxy.meili-inc.com/a/game/megret-docs/quickstart/",
                "_blank"
            );
        }
    }
};
</script>
<style scoped>
.menu-wrap {
    z-index: 1000;
    position: fixed;
    top: 100px;
    right: 100px;
}
</style>



