<template>
    <div class="runtime-wrap">

        <md-empty-state v-if="!game" md-label="Cannot find game" md-description="No gameId">
            <md-button class="md-primary md-raised" href="/create">Go to Manage Page</md-button>
        </md-empty-state>

        <md-card v-else>
            <md-card-content>
                <div class="cards">
                    <div
                        class="cards-runtime"
                        :class="{active: currentCard === 'runtime'}"
                        @click="currentCard = 'runtime'"
                    >runtime</div>
                    <div
                        class="cards-publish"
                        :class="{active: currentCard === 'publish'}"
                        @click="currentCard = 'publish'"
                    >publish</div>
                    <span class="card-active-line" ref="activeBar"></span>
                </div>

                <runtime v-if="game && currentCard === 'runtime'" ref="runtime" :game="game"></runtime>
                <publish v-if="game && currentCard === 'publish'" :game="game"></publish>

            </md-card-content>
        </md-card>
        <Menu></Menu>
    </div>
</template>

<script>

import Menu from "./components/menu";
import Runtime from "./components/runtime";
import Publish from "./components/publish";
import axios from "axios";

export default {
    components: {
        Menu,
        Runtime,
        Publish
    },
    data() {
        return {
            currentCard: "runtime",
            actingColor: "#fc5457",
            game: undefined
        };
    },
    mounted() {
        const gameId = this.getUrlParam("gameId");
        this.getGameById(gameId);
    },
    watch: {
        currentCard(newValue) {
            if (newValue === "runtime") {
                this.$refs.activeBar.style.left = "0%";
            } else {
                this.$refs.activeBar.style.left = "50%";
            }
        }
    },
    methods: {
        getGameById(gameId) {
            axios
                .get(`/api/v1/games?gameId=${gameId}`)
                .then(res => {
                    this.game = res.data.data;
                })
                .catch(err => {
                    this.$notify({
                        group: "foo",
                        type: "error",
                        text: `游戏运行失败，error: ${err}`
                    });
                });
        },
        getUrlParam(key) {
            const url = new URL(window.location);
            const value = url.searchParams.get(key);
            return value;
        }
    }
};
</script>

<style scoped>
.runtime-wrap {
    margin: 0 50px;
    font-family: "KaiTi", "Franklin Gothic Medium", "Arial Narrow", Arial,
        sans-serif;
}
.md-card-content {
    padding: 0;
}

.active {
    font-size: 16px;
    font-weight: bold;
    color: #488dfc;
}
.cards {
    display: flex;
    position: relative;
    width: 100%;
    height: 60px;
    cursor: pointer;
    background-image: linear-gradient(
        to bottom,
        rgb(255, 255, 255),
        rgb(240, 240, 240)
    );
}
.cards-runtime,
.cards-publish {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
    text-transform: uppercase;
}
.card-active-line {
    position: absolute;
    top: calc(100% - 2px);
    left: 0;
    width: 50%;
    height: 2px;
    background-color: #488dfc;
    transition: all 0.3s;
}
</style>



