<template>
    <div class="terminal-wrap" ref="codeContainer">
        <prism language="bash" :plugins="['command-line']" :code="output"></prism>
    </div>
</template>

<script>
import Prism from "./prism";
import "prismjs/themes/prism-tomorrow.css";

export default {
    components: {
        Prism
    },
    props: {
        output: {
            type: String,
            default: ""
        }
    },
    watch: {
        output() {
            const $codeContainer = this.$refs.codeContainer;
            $codeContainer.scrollTop = $codeContainer.scrollHeight;
        }
    }
};
</script>

<style scoped>
.terminal-wrap {
    height: 450px;
    overflow: scroll;
}
</style>



