<template>
    <div class="md-layout md-alignment-center-center game-wrap">
        <div class="md-layout-item md-size-40">
            <md-card>
                <md-card-header>
                    <img
                        class="game-logo"
                        src="https://s10.mogucdn.com/mlcdn/c45406/181203_1527gc43i9gjgfde0ljib138a13ic_615x214.png"
                    >
                    <!-- <div class="md-title game-title">新建游戏</div> -->
                </md-card-header>
                <md-card-content>
                    <div class="game-content md-layout-item md-size-60">
                        <!-- 游戏名（英） -->
                        <md-field md-clearable :class="{ 'md-invalid': gameNameChErr }">
                            <label>游戏名称(中文) : 游戏-{{gameNameCh}}</label>
                            <md-input
                                class="md-size-25"
                                @input="_inputGameNameCh"
                                v-model="gameNameCh"
                                required
                            ></md-input>
                            <span class="md-error">未填写游戏中文名</span>
                        </md-field>

                        <!-- 游戏名（中） -->
                        <md-field md-clearable :class="{ 'md-invalid': gameNameEnErr }">
                            <label>游戏名称(英文) : megret-{{gameType}}-{{gameNameEn}}</label>
                            <md-input v-model="gameNameEn" @input="_inputGameNameEn" required></md-input>
                            <span class="md-error">未填写游戏英文名</span>
                        </md-field>

                        <!-- 游戏类型 -->
                        <md-field :class="{ 'md-invalid': gameTypeErr }">
                            <label for="gameType">游戏类型 :</label>
                            <md-select
                                v-model="gameType"
                                @input="_changeGameType"
                                name="gameType"
                                id="gameType"
                            >
                                <md-option
                                    v-for="option in gameCategory"
                                    :key="_getKey(option)"
                                    :value="_getKey(option)"
                                >{{_getValue(option)}}</md-option>
                            </md-select>
                            <span class="md-error">未选择游戏类型</span>
                        </md-field>

                        <!-- 创建者 -->
                        <md-autocomplete
                            :class="{ 'md-invalid': creatorErr }"
                            v-model="creator"
                            @input="_changeCreator"
                            required
                            :md-options="creatorRepo"
                            :md-open-on-focus="false"
                        >
                            <label>花名拼音</label>
                            <span class="md-error">未填写花名</span>
                        </md-autocomplete>

                        <!-- 标签 -->
                        <md-chips
                            class="md-primary"
                            v-model="gameTags"
                            :md-limit="5"
                            md-placeholder="标签"
                        ></md-chips>

                        <!-- 描述 -->
                        <md-field>
                            <md-textarea v-model="gameDesc" placeholder="合作方；需求描述等信息"></md-textarea>
                        </md-field>

                        <div class="md-layout md-alignment-center-center">
                            <md-button class="create_btn" @click="_submit">创建</md-button>
                        </div>
                    </div>
                </md-card-content>
            </md-card>
        </div>
        <Menu></Menu>
        <create-dialog :socketOutput="socketOutput" :gameId="gameId" ref="createDialog"></create-dialog>
    </div>
</template>

<script>
import request from "../server/request";
import CreateDialog from "./components/createDialog";
import Menu from "./components/menu";

export default {
    name: "ClassicFormPage",
    components: {
        CreateDialog,
        Menu
    },
    data() {
        return {
            gameCategory: [],
            gameNameCh: "",
            gameNameEn: "",
            gameType: "default",
            creator: "",
            gameDesc: "",
            gameTags: [],
            creatorRepo: [],
            gameNameChErr: false,
            gameNameEnErr: false,
            gameTypeErr: false,
            creatorErr: false,
            gameId: undefined,
            socketOutput: "create gitlab repoisty\n"
        };
    },
    sockets: {
        connect() {},

        command(data) {
            this.socketOutput += `${data}\n`;
        },

        /**
         * crate repository on gitlab
         */
        createGitRepository(data) {
            this.socketOutput += `@out@${data}\n`;
        },

        /**
         * fetch code to local
         */
        fetchCodeToLocal(data) {
            this.socketOutput += `@out@${data}\n`;
        },

        successfull(data) {
            this.socketOutput += `${data}\n`;
            this.$refs.createDialog.showSuccessBtn();
        }
    },
    methods: {
        _request() {
            let data = {
                gameType: [{ default: "默认" }, { dragonBones: "骨骼" }, { answer: "答题" }]
            };

            this.gameCategory = data.gameType;
        },

        _getUser(val) {
            clearTimeout(this.timer);
            this.timer = null;
            this.timer = setTimeout(() => {
                request({
                    method: "GET",
                    url: "http://jcube.mogu-inc.com/searchUser",
                    params: {
                        q: val
                    }
                }).then(data => {
                    const creatorsOptions = (data && data.data && data.data.result) || [];
                    const creatorRepo = [];
                    creatorsOptions.forEach(element => {
                        creatorRepo.push(element.domain);
                    });
                    this.creatorRepo = creatorRepo;
                });
            }, 300);
        },

        _getKey(option) {
            return Object.keys(option)[0];
        },

        _getValue(option) {
            return Object.values(option)[0];
        },

        _inputGameNameCh(val) {
            this.gameNameChErr = !val;
        },

        _inputGameNameEn(val) {
            this.gameNameEnErr = !val;
        },

        _changeGameType(val) {
            this.gameTypeErr = !val;
        },

        _changeCreator(val) {
            this.creatorErr = !val;
            this._getUser(val);
        },

        _valiate() {
            this.gameNameChErr = !this.gameNameCh;
            this.gameNameEnErr = !this.gameNameEn;
            this.gameTypeErr = !this.gameType;
            this.creatorErr = !this.creator;

            return (
                !this.gameNameChErr &&
                !this.gameNameEnErr &&
                !this.gameTypeErr &&
                !this.creatorErr
            );
        },

        _errNotify(text = "", title = "Error") {
            this.$notify({
                group: "foo",
                type: "error",
                text: text
            });
        },

        _successNotify(text = "", title = "SUCCESS") {
            this.$notify({
                group: "foo",
                type: "success",
                title: title,
                text: text
            });
        },

        async _submit() {
            let isValiate = this._valiate();
            if (isValiate) {
                this.$refs.createDialog.showDialog();
                const params = {
                    gameNameEn: `megret-${this.gameType}-${this.gameNameEn}`,
                    gameNameCh: this.gameNameCh,
                    gameType: this.gameType,
                    creator: this.creator,
                    description: this.gameDesc,
                    tags: this.gameTags
                };
                try {
                    const res = await request({
                        url: "/api/v1/games",
                        method: "post",
                        data: params
                    });
                    this.gameId = res.data.data.gameId;
                } catch (err) {
                    this._errNotify(err.msg || "创建失败", "Not valivate");
                }
            } else {
                this._errNotify("表单字段验证失败", "Not valivate");
            }
        }
    },
    mounted() {
        this._request();
        this.timer = null;
    }
};
</script>

<style scoped>
.game-wrap {
    width: 100vw;
    min-height: 100vh;
    display: flex;
    align-items: center;
    font-family: "KaiTi", 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
}

.game-logo {
    display: block;
    width: 200px;
    height: 60px;
    margin: 0 auto 30px;
    border-radius: 0;
    float: none;
}

.game-title {
    font-size: 30px;
    text-align: center;
}

.game-content {
    margin: 0 auto;
}

.create_btn {
    margin: 0 auto;
    color: #6100e8;
}
.xterm-wrap {
    z-index: 100;
    position: fixed;
    opacity: 0.7;
}
</style>



