<template>
    <div class="spinner">
        <div class="double-bounce1" :style="{backgroundColor: themeColor}"></div>
        <div class="double-bounce2" :style="{backgroundColor: themeColor}"></div>
    </div>
</template>

<script>
export default {
    props: {
        themeColor: {
            type: String,
            default: "#f4ec51"
        }
    }
};
</script>

<style scoped>
.spinner {
    display: inline-block;
    width: 15px;
    height: 15px;

    position: relative;
    margin: 0 auto;
}

.double-bounce1,
.double-bounce2 {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    background-color: #f4ec51;
    opacity: 0.6;
    position: absolute;
    top: 0;
    left: 0;

    -webkit-animation: sk-bounce 2s infinite ease-in-out;
    animation: sk-bounce 2s infinite ease-in-out;
}

.double-bounce2 {
    -webkit-animation-delay: -1s;
    animation-delay: -1s;
}

@-webkit-keyframes sk-bounce {
    0%,
    100% {
        -webkit-transform: scale(0);
    }
    50% {
        -webkit-transform: scale(1);
    }
}

@keyframes sk-bounce {
    0%,
    100% {
        transform: scale(0);
        -webkit-transform: scale(0);
    }
    50% {
        transform: scale(1);
        -webkit-transform: scale(1);
    }
}
</style>



