<template>
    <div class="create-dialog-wrap">
        <md-dialog class="dilag-wrap" :md-active.sync="isShow" md-confirm-text="confirm">
            <md-dialog-title class="dialog-title">{{dialogTitle}}</md-dialog-title>
            <div class="backend-shell" ref="codeContainer">
                <prism language="bash" :plugins="['command-line']" :code="socketOutput"></prism>
            </div>
            <md-dialog-actions>
                <md-button class="md-primary" @click="isShow = false">close</md-button>
                <template v-if="success && gameId">
                    <md-button class="md-primary" @click="switchPage(`manage?gameId=${gameId}`)">Run!</md-button>
                </template>
                <template v-else>
                    <md-button class="md-primary" disabled @click="switchPage('list')">Run!</md-button>
                </template>
            </md-dialog-actions>
        </md-dialog>
    </div>
</template>

<script>
import Prism from "./prism";
import "prismjs/themes/prism-tomorrow.css";

export default {
    components: {
        Prism
    },
    props: {
        gameId: {
            type: Number | String | undefined,
            default: undefined
        },
        socketOutput: {
            type: String,
            default: "create egret game"
        }
    },
    watch: {
        socketOutput() {
            const $codeContainer = this.$refs.codeContainer;
            $codeContainer.scrollTop = $codeContainer.scrollHeight;
        }
    },
    data() {
        return {
            isShow: false,
            success: false,
            dialogTitle: "Terminal"
        };
    },
    mounted() {},
    methods: {
        switchPage(targetPage) {
            this.$router.push({ path: `/${targetPage}` });
        },
        showSuccessBtn() {
            this.success = true;
        },
        showDialog() {
            this.isShow = true;
        }
    }
};
</script>

<style scoped>
.md-dialog {
    background: #2d2d2d;
}
.md-dialog-title {
    margin-bottom: 0;
}
.dialog-close {
    color: #fff;
}
.dialog-title {
    padding: 12px;
    background: #000;
    color: #fff;
}
.backend-shell {
    width: 700px;
    height: 300px;
    overflow: scroll;
    padding: 0;
    background-color: #2d2d2d;
}
</style>



