<template>
    <div class="md-layout md-alignment-top-center home-wrap">
        <div class="md-layout-item md-size-20 card-item" @click="switchPage('create')">
            <md-card md-with-hover>
                <md-ripple>
                    <md-card-media>
                        <img
                            src="https://s10.mogucdn.com/mlcdn/c45406/181203_3h053lf5jh79chj4b5j451b2c50ac_1280x853.jpg"
                            alt="People"
                        >
                    </md-card-media>
                    <md-card-header>
                        <div class="md-title">新建游戏</div>
                    </md-card-header>
                </md-ripple>
            </md-card>
        </div>
        <div class="md-layout-item md-size-20 card-item" @click="switchPage('list')">
            <md-card md-with-hover>
                <md-ripple>
                    <md-card-media>
                        <img
                            src="https://s10.mogucdn.com/mlcdn/c45406/181203_46374de38j1f94h19l3dkklbcik75_1280x853.jpg"
                            alt="People"
                        >
                    </md-card-media>
                    <md-card-header>
                        <div class="md-title">游戏列表</div>
                    </md-card-header>
                </md-ripple>
            </md-card>
        </div>
    </div>
</template>

<script>
export default {
    components: {},
    methods: {
        switchPage(targetPage) {
            this.$router.push({ path: `/${targetPage}` });
        }
    }
};
</script>

<style scoped>
.home-wrap {
    font-family: "KaiTi", 'Franklin Gothic Medium', 'Arial Narrow', Arial, sans-serif;
    width: 100vw;
    height: 100vh;
    display: flex;
    align-items: center;
    margin-top: -30px;
}
.card-item {
    margin: 0 30px;
}
.md-title {
    font-size: 22px;
}
</style>



