<template>
    <div class="md-layout-item md-layout md-alignment-center-center md-gutter manage-wrap">
        <md-table v-model="searched" md-card @md-selected="onSelect">
            <md-table-toolbar>
                <h1 class="md-title">游戏管理</h1>
                <md-field md-clearable class="md-toolbar-section-end">
                    <md-input
                        placeholder="Search by gameId..."
                        v-model="search"
                        @input="searchOnTable"
                    />
                </md-field>
            </md-table-toolbar>

            <md-table-empty-state
                md-label="No games found"
                :md-description="`No game found for this '${search}' query. Try a different search term or create a new game.`"
            >
                <md-button
                    class="md-primary md-raised"
                    @click="switchPage('create')"
                >Create New Game</md-button>
            </md-table-empty-state>

            <md-table-row
                slot="md-table-row"
                slot-scope="{ item }"
                :class="item.status"
                md-selectable="single"
            >
                <md-table-cell md-label="ID" md-sort-by="id" md-numeric>{{ item.gameId }}</md-table-cell>
                <md-table-cell md-label="游戏名" md-sort-by="email">{{ item.gameNameCh }}</md-table-cell>
                <md-table-cell md-label="简述" md-sort-by="title">{{ item.description }}</md-table-cell>
                <md-table-cell
                    md-label="创建时间"
                    md-sort-by="createAt"
                >{{ item.createAt | moment("YYYY-MM-DD h:mm:ss") }}</md-table-cell>
                <md-table-cell md-label="创建者" md-sort-by="gender">{{ item.creator }}</md-table-cell>
                <md-table-cell md-label="gitlab" md-sort-by="name">
                    <i
                        class="iconfont icon-link"
                        style="color: blue"
                        @click.prevent.stop="openGitlab(item.webUrl)"
                    ></i>
                </md-table-cell>
                <md-table-cell
                    md-label="status"
                    md-sort-by="status"
                >{{ item.status || "uninstalled" }}</md-table-cell>
                <md-table-cell md-label="action" md-sort-by="action">
                    <i
                        v-if="item.status === 'uninstalled'"
                        class="iconfont icon-download"
                        style="color: green"
                        @click.prevent.stop="_installGame(item.gameId)"
                    ></i>
                    <i v-if="item.status === 'installing'" class="iconfont icon-loading"></i>
                    <i
                        v-if="item.status === 'installed'"
                        class="iconfont icon-runo24"
                        style="color: red"
                        @click="_runGame(item)"
                    ></i>
                    <Acting v-if="item.status === 'running'"></Acting>
                    <i
                        v-if="deleteCtrl"
                        class="iconfont icon-close"
                        @click.stop="_deleteGame(item.gameId)"
                    ></i>
                </md-table-cell>
            </md-table-row>
        </md-table>
        <Menu></Menu>
    </div>
</template>

<script>
import axios from "axios";
import Menu from "./components/menu";
import Acting from "./components/acting";

export default {
    name: "gameListPage",
    components: {
        Menu,
        Acting
    },
    data() {
        return {
            search: null,
            deleteCtrl: false,
            searched: [],
            games: []
        };
    },

    mounted() {
        // hidden method: delete game
        const url = new URL(window.location);
        const value = url.searchParams.get("deleteProject");
        this.deleteCtrl = value === "5858";

        this._getGames();
    },

    methods: {
        /**
         * 获取游戏列表
         */
        _getGames() {
            axios.get("/api/v1/games").then(res => {
                this.games = res.data.data;
                this.searched = this.games;
            });
        },

        /**
         *  将game下载至本地
         */
        _installGame(gameId) {
            const currentGameIndex = this.getGameIndexByGameId(gameId);
            this.games[currentGameIndex].status = "installing";

            const params = {
                gameId
            };

            axios
                .request({
                    url: "/api/v1/install",
                    method: "post",
                    data: params
                })
                .then(res => {
                    this.games[currentGameIndex].status = "installed";
                    this.$notify({
                        group: "foo",
                        type: "success",
                        title: "SUCCESS",
                        text: "本地安装完成"
                    });
                })
                .catch(err => {
                    this.games[currentGameIndex].status = "uninstalled";
                    this.$notify({
                        group: "foo",
                        type: "error",
                        text: `安装失败，error: ${err}`
                    });
                });
        },

        /**
         * 运行game
         */
        _runGame(item) {
            this.$router.push({
                path: `/manage?gameId=${item.gameId}`
            });
        },

        /*
         * 删除游戏(临时功能)
         */
        _deleteGame(gameId) {
            axios
                .request({
                    url: "/api/v1/game",
                    method: "DELETE",
                    data: {
                        gameId: gameId
                    }
                })
                .then(res => {
                    console.log(res);
                });
        },

        openGitlab(gitlab) {
            window.open(gitlab, "_blank");
        },

        getGameIndexByGameId(gameId) {
            if (this.games.length > 0) {
                for (let i = 0; i < this.games.length; i++) {
                    if (this.games[i].gameId === gameId) {
                        return i;
                    }
                }
            }
            return -1;
        },

        switchPage(targetPage) {
            this.$router.push({ path: `/${targetPage}` });
        },

        toLower(text) {
            return text.toString().toLowerCase();
        },

        searchByName(items, term) {
            if (term) {
                return items.filter(item =>
                    this.toLower(item.gameId).includes(this.toLower(term))
                );
            }
            return items;
        },

        searchOnTable() {
            this.searched = this.searchByName(this.games, this.search);
        },

        getClass(item) {
            if (item.status === "installed") {
                return "installed-bg";
            } else if (item.status === "running") {
                return "running-bg";
            } else {
                return "";
            }
        },

        onSelect(item) {
            if (item && item.status && item.status !== "uninstalled") {
                this.$router.push({ path: `/manage?gameId=${item.gameId}` });
            } else {
                item && item.gameId && this._installGame(item.gameId);
            }
        }
    }
};
</script>

<style scoped>
.manage-wrap {
    font-family: "KaiTi", "Franklin Gothic Medium", "Arial Narrow", Arial,
        sans-serif;
    padding: 30px;
}
.game-item {
    margin-right: 30px;
}
</style>

<style scoped>
#actions .icon-loading {
    display: inline-block;
    color: red;
    animation: spin 1s linear infinite;
}

.running {
    color: #fff;
    background-color: #f36b5a !important;
}
.installed {
    background-color: rgba(93, 207, 144, 0.2);
}

@keyframes spin {
    0% {
        transform: rotate(0);
    }
    100% {
        transform: rotate(360deg);
    }
}
</style>



