<template>
    <div class="content">
        <div class="info-panel">
            <div class="info-panel-title">Game Info</div>
            <div class="viewport">
                <md-list>
                    <md-list-item>
                        <i class="iconfont icon-iddenglu" style="color: red"></i>
                        <span class="md-list-item-text">{{ game.gameId }}</span>
                    </md-list-item>
                    <md-divider class="md-inset"></md-divider>

                    <md-list-item>
                        <i class="iconfont icon-game" style="color: #e16d74"></i>
                        <span
                            class="md-list-item-text"
                        >{{ game.gameNameCh}} ( {{ game.gameNameEn }} )</span>
                    </md-list-item>
                    <md-divider class="md-inset"></md-divider>

                    <md-list-item>
                        <i class="iconfont icon-people" style="color: purple"></i>
                        <span class="md-list-item-text">{{ game.creator }}</span>
                    </md-list-item>
                    <md-divider class="md-inset"></md-divider>

                    <md-list-item>
                        <i class="iconfont icon-miaoshu" style="color: green"></i>
                        <span class="md-list-item-text">{{ game.description }}</span>
                    </md-list-item>
                    <md-divider class="md-inset"></md-divider>

                    <md-list-item>
                        <i class="iconfont icon-time" style="color: #ff5777"></i>
                        <span
                            class="md-list-item-text"
                        >{{ game.createAt | moment("YYYY-MM-DD / h:mm:ss") }}</span>
                    </md-list-item>
                    <md-divider class="md-inset"></md-divider>

                    <md-list-item>
                        <i class="iconfont icon-gitlab" style="color: #df4431"></i>
                        <a
                            class="md-list-item-text"
                            :href="game.webUrl"
                            target="_blank"
                        >{{ game.webUrl }}</a>
                    </md-list-item>
                    <md-divider class="md-inset"></md-divider>

                    <md-list-item>
                        <i
                            class="iconfont icon-ecs-running"
                            :class="{spining: gameStatus != 'stopped'}"
                        ></i>
                        <span class="md-list-item-text">{{ gameStatus }}</span>
                        <a
                            v-if="ipPort"
                            :href="ipPort"
                            target="_blank"
                            style="width:75%"
                        >{{ ipPort }}</a>
                    </md-list-item>
                    <md-divider class="md-inset"></md-divider>
                </md-list>
                <div class="game-ctrl">
                    <md-button
                        v-if="gameStatus ==='starting'"
                        class="md-dense md-raised md-accent"
                        disabled
                        @click="stopGame"
                    >启动中</md-button>
                    <md-button
                        v-if="gameStatus === 'running'"
                        class="md-dense md-raised md-accent"
                        @click="stopGame"
                    >停止</md-button>
                    <md-button
                        v-if="gameStatus === 'stopped'"
                        class="md-dense md-raised md-accent"
                        @click="runGame"
                    >启动</md-button>
                </div>
            </div>
        </div>
        <div class="terminal-panel">
            <div class="terminal-panel-title">Terminal</div>
            <Terminal :output="terminalOutput"></Terminal>
        </div>
    </div>
</template>

<script>
import Terminal from "./terminal";
import axios from "axios";
export default {
    components: {
        Terminal
    },
    props: ["game"],
    sockets: {
        connect() {},

        egretInstallWarning() {
            this.$notify({
                group: "foo",
                type: "error",
                text:
                    "请先安装egret engine: \nhttps://egret.com/en/downloads/engine/type/stable.html\n"
            });
        },

        command(data) {
            this.terminalOutput += `${data}\n`;
        },

        runningStdout(data) {
            this.terminalOutput += `@out@${data}\n`;
        },

        egretServerStarted(data) {
            this.$notify({
                group: "foo",
                type: "success",
                title: "SUCCESS",
                text: `egret server on ${data}`
            });
            this.ipPort = data.replace(/\s+/, "");
            this.gameStatus = "running";
        },

        stopRunningGame(data) {
            this.terminalOutput += `\n${data}\n`;
        },

        runningStderr(data) {
            this.terminalOutput += `@out@${data}\n`;
        }
    },

    data() {
        return {
            terminalOutput: "egret process manage\n",
            ipPort: "",
            gameStatus: "stopped"
        };
    },

    mounted() {
        if (this.game.ipPort) {
            this.terminalOutput = `egret is running on...\n\n${
                this.game.ipPort
            }\n`;
            this.ipPort = this.game.ipPort;
            this.gameStatus = "running";
        }
    },

    methods: {
        runGame() {
            this.gameStatus = "starting";
            const params = {
                gameId: `${this.game.gameId}`,
                gameNameEn: this.game.gameNameEn
            };

            axios
                .request({
                    url: "/api/v1/run",
                    method: "post",
                    data: params
                })
                .then(res => {
                    if (res.data.data && res.data.data.ipPort) {
                        const ipPort = res.data.data.ipPort;
                        this.gameStatus = "running";

                        this.terminalOutput = `egret is running on...\n\n${ipPort}\n`;
                    }
                })
                .catch(err => {
                    this.$notify({
                        group: "foo",
                        type: "error",
                        text: `游戏运行失败，error: ${err}`
                    });
                });
        },
        stopGame() {
            const gameId = `${this.game.gameId}`;
            axios
                .request({
                    url: "/api/v1/game/runningstatus",
                    method: "DELETE",
                    data: { gameId }
                })
                .then(res => {
                    this.gameStatus = "stopped";
                    this.ipPort = "";
                });
        }
    }
};
</script>

<style scoped>
.content {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: auto;
    padding: 15px 0 50px;
}

.info-panel,
.terminal-panel {
    width: 49%;
    height: 500px;
    margin: 0 10px;
    box-shadow: 0 0 6px 0 #eee;
}
.terminal-panel {
    background-color: #2d2d2d;
}
.info-panel-title,
.terminal-panel-title {
    display: flex;
    align-items: center;
    width: 100%;
    height: 50px;
    font-size: 18px;
    font-weight: bold;
    padding-left: 15px;
    color: #fff;
    background-color: #488dfc;
}
.terminal-panel-title {
    color: #fff;
    background-color: #000;
}
.viewport {
    display: inline-block;
    width: 100%;
    vertical-align: top;
    overflow: auto;
    border: 1px solid rgba(#000, 0.12);
}
.iconfont {
    margin: 0 20px 0 10px;
}
.md-divider.md-inset {
    margin: 0 10px;
}
.game-ctrl {
    display: flex;
    justify-content: flex-end;
    margin: 10px;
}
.spining {
    color: red;
    display: inline-block;
    animation: spin 2s linear infinite backwards;
}
@keyframes spin {
    0% {
        transform: rotate(0);
    }
    100% {
        transform: rotate(360deg);
    }
}
</style>



