<template>
    <div class="publish-wrap">
        <md-steppers :md-active-step.sync="active" md-vertical md-linear>
            <md-step
                id="first"
                md-label="First Step"
                md-description="选择目录进行图片压缩"
                :md-editable="false"
                :md-done.sync="first"
            >
                <md-checkbox
                    v-for="(fileName, index) in compressTargets"
                    :key="index"
                    v-model="selectedFiles"
                    :value="fileName"
                >{{ fileName }}</md-checkbox>
                <md-divider/>

                <div class="compress-bar-label">图片质量</div>
                <input type="range" min="30" max="99" v-model.number="quality">
                {{ quality }}%
                <!-- origin size -->
                <template v-if="originSize">
                    <i class="iconfont icon-croporiginal"></i>
                    <span class="origin-size">{{ originSize }}</span>
                </template>

                <!-- compressed size -->
                <template v-if="compressedSize">
                    <i class="iconfont icon-compress"></i>
                    <span class="origin-size">{{ compressedSize }}</span>
                </template>

                <!-- compressed rate -->
                <template v-if="compressedRate">
                    <i class="iconfont icon-rate"></i>
                    <span class="origin-size">{{ compressedRate }}</span>
                </template>

                <md-divider/>

                <!-- continue btn -->
                <md-button
                    class="md-raised md-primary compress-btn"
                    v-if="(!selectedFiles.length || compressStatus === 'compressed')"
                    @click="setDone('first', 'second')"
                >继续</md-button>

                <!-- loading btn -->
                <md-button
                    class="md-raised md-primary compress-btn"
                    disabled
                    v-if="compressStatus === 'compressing'"
                >loading...</md-button>

                <!-- compressed btn -->
                <md-button
                    v-if="selectedFiles.length > 0 && compressStatus != 'compressing' && compressStatus !='compressed'"
                    class="md-raised md-accent compress-btn"
                    @click="compressImage"
                >compress</md-button>
            </md-step>

            <md-step
                id="second"
                md-label="Second Step"
                :md-error="secondStepError"
                :md-editable="false"
                :md-done.sync="second"
                md-description="发布项目"
            >
                <p>游戏发布</p>
                <md-field md-inline>
                    <label>Description</label>
                    <md-textarea v-model="description"></md-textarea>
                </md-field>
                <div class="publish-btn-wrap" v-if="!publishing">
                    <md-button class="md-raised md-primary" @click="publishProject()">确认发布</md-button>
                </div>
                <div class="publish-btn-wrap" v-else>
                    <md-button class="md-raised md-primary" disabled>发布中
                        <div class="publishing-wrap">
                            <div class="publishing"></div>
                        </div>
                    </md-button>
                </div>
            </md-step>

            <md-step
                id="third"
                md-label="Third Step"
                :md-editable="false"
                :md-done.sync="third"
                md-description="发布完成"
            >
                <div v-if="publishRes">
                    <md-list>
                        <md-list-item>
                            <span>
                                <i class="iconfont icon-version" style="color: #ff4466;"></i>
                                <span class="publish-label">发布版本 :</span>
                            </span>
                            <div class="publish-result-text">{{publishRes.version}}</div>
                        </md-list-item>
                        <md-list-item>
                            <span>
                                <i class="iconfont icon-description" style="color: #ff0000;"></i>
                                <span class="publish-label">发布描述 :</span>
                            </span>
                            <div class="publish-result-text">{{publishRes.description}}</div>
                        </md-list-item>
                        <md-list-item>
                            <span>
                                <i class="iconfont icon-cdn" style="color: #66a8eb;"></i>
                                <span class="publish-label">静态资源 :</span>
                            </span>
                            <div class="publish-result-text">{{publishRes.resCdn}}</div>
                        </md-list-item>
                        <md-list-item>
                            <span>
                                <i class="iconfont icon-cdn" style="color: #66a8eb;"></i>
                                <span class="publish-label">main.js资源 :</span>
                            </span>
                            <div class="publish-result-text">{{publishRes.mainJsCdn}}</div>
                        </md-list-item>
                    </md-list>
                </div>
                <md-button class="md-raised md-primary" @click="cachePublish()">确认</md-button>
                <md-button class="md-raised md-primary" @click="republish()">取消</md-button>
            </md-step>
        </md-steppers>
    </div>
</template>

<script>
import axios from "axios";

export default {
    props: ["game"],
    data() {
        return {
            active: "first",
            first: false,
            second: false,
            third: false,
            isTrueHost: false,
            secondStepError: null,
            compressTargets: [],
            selectedFiles: [],
            quality: 65,
            originSize: 0,
            compressedSize: "",
            compressedRate: "",
            compressStatus: "noCompress",
            publishing: false,
            version: "",
            description: "",
            publishRes: {
                version: "0.0.1",
                description: "测试发布",
                mainJsCdn:
                    "https://s10.mogucdn.com/mlcdn/c45406/181227_7d024521j89a6df59cl5eh0aaida3.json",
                resCdn:
                    "https://s10.mogucdn.com/mlcdn/c45406/181227_7d024521j89a6df59cl5eh0aaida3.json"
            }
        };
    },

    mounted() {
        this.getResourceDir();

        const host = window.location.host;
        if (!host.match(/.mogu-inc.com/, "g")) {
            this.$notify({
                group: "foo",
                type: "error",
                text: "请切换到 xxx.mogu-inc.com 的域名下，再进行发布"
            });
        } else {
            let ticket = this.$route.query.ticket || "";

            axios.get(`/api/v1/loginInfo?ticket=${ticket}`).then(response => {
                if (response.data.code === 1001) {
                    this.isTrueHost = false;
                    window.location.href =
                        "http:" +
                        "//accounts.mogu-inc.com/enter?redirect=" +
                        window.location.href;
                } else {
                    this.isTrueHost = true;
                }
            });
        }
    },

    methods: {
        setDone(id, index) {
            this[id] = true;
            this.secondStepError = null;

            if (index) {
                this.active = index;
            }
        },
        republish() {
            this.first = false;
            this.second = false;
            this.third = false;
            this.active = "first";
        },
        setError() {
            this.secondStepError = "This is an error!";
        },

        publishProject() {
            if (!this.isTrueHost) {
                this.$notify({
                    group: "foo",
                    type: "error",
                    text: "请切换到 xxx.mogu-inc.com 的域名下，再进行发布"
                });
                return;
            }
            const params = {
                description: this.description,
                gameId: this.game.gameId
            };
            this.publishing = true;
            axios
                .request({
                    url: "/api/v1/game/release",
                    method: "post",
                    data: params
                })
                .then(res => {
                    this.publishing = false;
                    const data = res.data;
                    if (data.msg === "SUCCESS") {
                        this.publishRes = data.data;
                        this.second = true;
                        this.active = "third";
                    } else if (data.code === 1001) {
                        window.location.href =
                            "http:" +
                            "//accounts.mogu-inc.com/enter?redirect=" +
                            window.location.href;
                    }
                })
                .catch(err => {
                    console.log(err);
                    this.publishing = false;
                });
        },

        compressImage() {
            this.compressStatus = "compressing";
            axios
                .request({
                    url: "/api/v1/game/targetFiles",
                    method: "put",
                    data: {
                        gameNameEn: this.game.gameNameEn,
                        targetFiles: this.selectedFiles,
                        quality: this.quality
                    }
                })
                .then(res => {
                    this.compressStatus = "compressed";
                    if (res.data.code === 200 && res.data.data) {
                        const data = res.data.data;
                        this.originSize = data.preCompressSize
                            .split("/Users")[0]
                            .replace(/\s+/, "");
                        this.compressedSize = data.afterCompressSize
                            .split("/Users")[0]
                            .replace(/\s+/, "");
                    }
                });
        },

        cachePublish() {
            const params = Object.assign({}, this.publishRes, {
                gameId: this.game.gameId
            });
            axios.request({
                url: "/api/v1/game/cachePublish",
                method: "post",
                data: params
            }).then(res => {
                if (res.data.code === 200) {
                    this.$notify({
                        group: "foo",
                        type: "info",
                        text: "信息保存成功"
                    });
                } else if (res.data.code === 1011) {
                    this.$notify({
                        group: "foo",
                        type: "error",
                        text: "信息已经保持过了！"
                    });
                }
            });
        },

        async getResourceDir() {
            const gameNameEn = this.game.gameNameEn;
            try {
                const res = await axios.request({
                    url: `/api/v1/game/resourceDir?gameNameEn=${gameNameEn}`,
                    method: "get"
                });
                const filelist = res.data.data.filelist;
                this.compressTargets = filelist;
            } catch (err) {}
        }
    }
};
</script>

<style scoped>
.publish-wrap {
    display: flex;
    flex-direction: row;
    width: 100%;
    min-height: 500px;
    padding: 15px 0 50px;
}
.compress-bar-label {
    margin: 15px 0 10px;
}
.compress-btn {
    margin-top: 30px;
}
.icon-croporiginal {
    color: orange;
    margin: 0 -5px 0 10px;
}
.icon-compress {
    color: green;
    margin: 0 -5px 0 20px;
}
.icon-rate {
    color: red;
    margin: 0 -5px 0 20px;
}

.publish-label {
    margin-left: 10px;
}

.publish-result-text {
    width: 600px;

    margin-left: 20px;
}

.publishing-wrap {
    display: inline-block;
    width: 15px;
}

.publishing {
    position: relative;
    display: inline-block;
    width: 15px;
    height: 5px;
    top: 50%;
    transform: translateY(-50%);
    margin-left: 5px;

    background: #efefef;

    animation: loading 5s infinite ease-in-out;
}

@keyframes loading {
    0% {
        width: 0px;
        background: #efefef;
    }
    50% {
        width: 15px;
        background: #2e8afa;
    }
    100% {
        width: 0px;
        background: #efefef;
    }
}
</style>



